.app__footer {
  width: 100%;
  position: relative;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  background-color: black;
}
.app__footer_links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: flex-start;
  margin-top: 5rem;
}
.app__footer_links-contact,
.app__footer_links-logo,
.app__footer_links-work {
  flex: 1;
  margin: 1rem;
  text-align: center;
}
.app__footer_links-logo img:first-child {
  width: 210px;
  margin-bottom: 0.75rem;
}
.app__footer_links-work:nth-child(2n + 1) {
  margin-bottom: 1rem;
}
.app__footer_links-icons {
  margin-top: 0.5rem;
}
.app__footer_links-icons svg {
  color: white;
  margin: 0.5rem;
  height: 30px;
  width: 30px;
}
.app__footer-headtext {
  color: aliceblue;
  margin: 1rem 0rem; 
}
@media screen and (max-width: 600px) {
  .app__footer_links {
    padding: 0;
    flex-direction: column;
    max-width: 100%;
    align-items: center;
  }
  .app__footer_links-logo img:first-child{
    width: 150px;
    margin: 1rem 2rem 1rem 2rem ;
  }
}
