.app__navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-golden);
  border: 2px solid black;
  padding: 1rem 2rem;
}
.app__navbar-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.app__navbar-logo img {
  width: 250px;
}
.app__navbar-links {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}
.app__navbar ul {
  list-style: none;
}
.app__navbar-links li {
  margin: 0 1rem;
  cursor: pointer;
}
.app__navbar-links li:hover {
  color: coral;
}
.app__navbar-login {
  display: flex;
  justify-content: center;
  align-items: center;
}
.app__navbar-login a {
  margin: 0rem 1rem;
  text-decoration: none;
  transition: 0.5s ease;
}
.app__navbar-login a:hover {
  border-bottom: 1px solid coral;
}
.app__navbar-login div {
  width: 1px;
  height: 30px;
  background-color: gray;
}
/* .app__navbar-smallscreen{
  display: none;
} */
.app__navbar-smallscreen_overlay{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: black;
  transition: 0.5s ease;
  flex-direction: column;
  z-index: 5;
}
.app__navbar-smallscreen_overlay .overlay__close{
  font-size: 27px;
  color: orange;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}
@media screen and (max-width:2000px) {
  .app__navbar-logo img{
    width: 210px;
  }
}


@media screen and (max-width:1000px) {
  .app__navbar-links{
    display: none;
  }
  .app__navbar-smallscreen{
    display: flex;
  }
}
@media screen and (max-width:650px) {
  .app__navbar{
    padding: 1rem;
  }
  .app__navbar-login{
    display:none;
  }
  .app__navbar-logo img{
    width: 110px;
  }
}

.app__navbar-smallscreen-links{
  direction: none;
}
.app__navbar-smallscreen-links li{
  margin: 3rem;
  cursor: pointer;
  font-size: 2rem;
  font-family: base;
}
.app__navbar-smallscreen-links li:hover{
  color: orange;
}
.app__navbar-logo img{
   width: 110px;
}