.app__specialMenu {
  flex-direction: column;
  background-color: #0d0d0d;}
.app__specialMenu-title {
  margin-bottom: 2rem;
  text-align: center;
}
.app__specialMenu-menu {
  width: 100%;
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
}
.app__specailMenu-menu_heading{
    font-family: var(--font-base);  
    font-weight: 600;
    font-size: 45px;
    line-height: 58px;
    letter-spacing: 0.04em;
    color: white;
}
.app__specialMenu-menu_wine, .app__specialMenu-menu_cocktails{
    flex: 1;
    width: 100%;
    flex-direction: column;

}
.app__specialMenu-menu_img{
    width: 400px;
    margin: 0 2rem;

}
.app__specialMenu-menu_img img{
    width: 100%;
    height: auto;
}
.app__specialMenu-menu_items{
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    width: 100%;
}
@media screen and (max-width:2000px) {}
@media screen and (max-width:1000px) {
    .app__specialMenu-menu{
        flex-direction: column;
        align-items: center;
        width: 100%;

    }
    .app__specailMenu-menu_heading{
        margin-top: 3rem;
    }
}
@media screen and (max-width:600px) {
    .app__specialMenu-menu_img{
        width: 100%;

    }
    .app__specailMenu-menu_heading{
        font-size: 36px;
        line-height: 100px;
    }
}