.app__gallery {
  display: flex;
  justify-content: center;
  /* flex-direction: row; */
  background-color: black;
  padding: 4rem 0 4rem 0;
}
#p__photos {
  color: #aaa;
  margin: 2rem;
  margin-left: 0;
}
.app__gallary_content {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-width: 500px;
  padding-right: 2rem;
  margin-left: 1rem;
}
.app__gallery_button {
  margin-top: 2rem;
}
.app__gallery_photos {
  flex: 1;
  flex-direction: row;
  max-width: 50%;
  display: flex;
  position: relative;
}
.app__gallery_photos-container {
  display: flex;
  flex-direction: row;
  width: max-content;
  overflow-x: scroll;
  -ms-overflow-style: none;
}
.app__gallery_photos-container::-webkit-scrollbar {
  display: none;
}
.app__gallery_photos-card {
  position: relative;
  min-width: 301px;
  height: 447px;
  margin-right: 2rem;
}
.app__gallery_photos-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: 0.5s ease;
}
.app__gallery_photos-card:hover img {
  opacity: 0.4;
}
.gallery__image_icon {
  position: absolute;
  font-size: 2rem;
  /* color: #fff; */
  opacity: 0;
  cursor: pointer;
  transition: 0.5s ease;
  color: #fff;
}
.app__gallery_photos-card:hover .gallery__image_icon {
  opacity: 1;
}
.app__gallery_photos-arrow {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  position: absolute;
  bottom: 5%;
}
.gallery__arrow_icon {
  font-size: 2rem;
  cursor: pointer;
  background-color: black;
  border-radius: 50px;
  color: #fff;
}
.gallery__arrow_icon:hover {
  color: orange;
  transition: 1s ease;
}
@media screen and (min-width: 2000px) {
  .app__gallary_content {
    min-width: 800px;
    padding-right: 2rem;
  }
}
@media screen and (max-width: 1150px) {
  .app__gallery {
    display: flex;
    flex-direction: column;
  }
  .app__gallary_content {
    margin-bottom: 2rem;
  }
  .app__gallery_photos {
    max-width: 100%;
  }
  .app__gallery_photos-card {
    margin-right: 3rem;
    min-width: 375px;
  }
}
@media screen and (max-width: 600px) {
  .app__gallary_content {
    min-width: 70%;
    padding-right: 0rem;
  }
  .app__gallery_photos-card {
    margin-right: 1rem;
    min-width: 47%;
    height: 400px;
  }
}
