.app__news {
  z-index: 1;
  padding: 1rem 4rem;
  border: 1px solid orange;
  background-color: black;
}
.app__news_heading {
  text-align: center;
}
.app__news_input {
  display: flex;
  flex-direction: row;
  margin-top: 3rem;
}
.app__news_input input {
  width: 620px;
  border: 1px solid orange;
  border-radius: 5px;
  margin-right: 4rem;
  height: 40px;
  font-size: 1rem;
  font-family: base;
  color: white;
  background-color: black;
  text-align: center;
}
.app__news_input button {
  width: max-content;
}
@media screen and (min-width: 2000px) {
  .app__news {
    max-width: 120%;
  }
  .app__news_input {
    max-width: 120%;
    margin:3rem 0rem 1rem 0rem ;
  }
  .app__news_input input {
    width: 100%;
    height: 85px;
    font-size: 2rem;
  }
  .app__news_input button {
    max-width: 100%;
    height: 85px;

  }
}
@media screen and (max-width: 1200px) {
  .app__news_input{
    margin:2rem 0rem 0.5rem 0rem ;
  }
  .app__news_input button {
    height: 40px;
  }
}
@media screen and (max-width: 900px) {
  .app__news{
    max-width: 70%;
  }
  .app__news_heading h1{
    font-size: 2.5rem;
  }
  .app__news_input{
    align-items: center;
    flex-direction: column;
    max-width: 100%;
  }
  .app__news_input input{
    max-width: 100%;
    margin-bottom: 1rem;
    margin-right: 0;
  }

}
