.app__laurels_awards{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 3rem;
    flex-wrap: wrap;
}
.app__bg{
    display: flex;
    width: 100%;
}
.app__wrapper_img{
    margin-top:3rem ;
}
.app__laurels_awards-card{
    width: 100%;
    display: flex;
    flex: 1;
    align-items: flex-start;
    justify-content: flex-start;
    min-width: 230px;
    margin-right: 1rem;
}
@media screen and (min-widt:1900px) {
    .app__laurels_awards-card{
        min-width: 390px;
    }
}
@media screen and (min-widt:450px) {
    .app__laurels_awards-card{
        min-width: 100%;
        margin: 1rem 0;
    }
}
.app__laurels_awards-card img{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-right: 0.5rem;
    width: 70px;
    height: 70px;
}
.app__laurels_awards-card_content{
    display: flex;
    flex-direction: column;
    margin:0 0 1rem;
}