.app__bg {
  display: flex;
}
.app__wrapper-img {
  width: 1200px;
  height: 500px;
  margin: 0 2rem 0 0;
}
.headtext__cormrnt {
  color: orange;
}
.app__wrapper-img img {
  width: 100%;
  height: 100%;
}
.app__chef-sign {
  width: 100%;
  margin-top: 3rem;
}
.app__chef-sign p {
  font-family: base;
  font-weight: 400;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color: orange;
}
.app__chef-sign img {
  width: 240px;
  margin-top: 3rem;
}
@media screen and (max-width: 2000px) {
  .app__chef-sign img {
    width: 380px;
  }
}
@media screen and (max-width: 1000px) {
  .app__chef-sign img {
    width: 80%;
  }
}
.app__wrapper-img {
  width: 70%;
}
@media screen and (max-width: 600px) {
  .app__wrapper-img {
    width: 90%;
  }
  .app__chef-sign img {
    width: 80%;
  }
}
